"use client";

import { usePathname, useRouter } from "next/navigation";
import { Home, Utensils, ListTodo, User } from "lucide-react";

export default function BottomBar() {
  const pathname = usePathname();
  const router = useRouter();

  const tabs = [
    { label: "Home", icon: <Home size={22} />, path: "/home" },
    { label: "Refeições", icon: <Utensils size={22} />, path: "/meals" },
    { label: "Lista", icon: <ListTodo size={22} />, path: "/shopping" },
    { label: "Perfil", icon: <User size={22} />, path: "/profile" },
  ];

  return (
    <nav className="fixed bottom-0 left-0 right-0 h-16 bg-white border-t border-gray-200 flex justify-around items-center z-40">
      {tabs.map((tab) => {
        const active = pathname === tab.path;

        return (
          <button
            key={tab.path}
            onClick={() => router.push(tab.path)}
            className="flex flex-col items-center justify-center"
          >
            <div
              className={`${
                active ? "text-[#00C974]" : "text-gray-400"
              } transition`}
            >
              {tab.icon}
            </div>
          </button>
        );
      })}
    </nav>
  );
}
