"use client";

import { Camera, Utensils, Droplet, Weight } from "lucide-react";
import { useRouter } from "next/navigation";

type Props = {
  open: boolean;
};

export default function ActionMenu({ open }: Props) {
  const router = useRouter();

  const actions = [
    {
      label: "Registrar refeição",
      icon: <Utensils size={22} />,
      route: "/meals/new",
    },
    {
      label: "Registrar água",
      icon: <Droplet size={22} />,
      route: "/water",
    },
    {
      label: "Registrar peso",
      icon: <Weight size={22} />,
      route: "/progress/weight",
    },
    {
      label: "Foto de progresso",
      icon: <Camera size={22} />,
      route: "/progress/photo",
    },
  ];

  if (!open) return null;

  return (
    <div className="fixed bottom-40 right-5 z-50 flex flex-col gap-3">
      {actions.map((a, index) => (
        <button
          key={a.label}
          onClick={() => router.push(a.route)}
          className="w-48 bg-white shadow-md rounded-xl py-3 px-4 flex items-center gap-3 animate-fadeSlideUp"
          style={{ animationDelay: `${index * 60}ms` }}
        >
          <span className="text-[#00C974]">{a.icon}</span>
          <span className="text-[#0A0A0A] font-medium">{a.label}</span>
        </button>
      ))}
    </div>
  );
}
