"use client";

import { useState } from "react";
import { Plus } from "lucide-react";
import ActionMenu from "./ActionMenu";

export default function FloatingActionButton() {
  const [open, setOpen] = useState(false);

  return (
    <>
      {/* FUNDO ESCURO AO ABRIR O MENU */}
      {open && (
        <div
          onClick={() => setOpen(false)}
          className="fixed inset-0 bg-black/30 z-40 animate-fadeIn"
        />
      )}

      {/* BOTÃO FLUTUANTE */}
      <button
        onClick={() => setOpen(!open)}
        className="fixed bottom-20 right-5 w-16 h-16 rounded-full bg-[#00C974] shadow-lg flex items-center justify-center z-50 transition hover:bg-[#00B368]"
      >
        <Plus size={32} className="text-white" />
      </button>

      {/* MENU DE AÇÕES */}
      <ActionMenu open={open} />
    </>
  );
}
